button {
    outline: none;
}

.todo {
    display: grid;
    padding: 1.8rem 2.5rem;
    grid-column-gap: 1rem;
    border-radius: 4px;
    box-shadow: 0 0 0 6px rgba(190, 137, 77, 0.473), 0 1rem 1rem 1px rgba(0, 0, 0, 0.479);
    grid-template-columns: 1fr minmax(-webkit-min-content, 10rem);
    grid-template-columns: 1fr minmax(min-content, 10rem);
    align-items: start;
    background-color: rgba(255, 255, 255, 1);
    /* border-radius: 10px; */
    
}

.todo-main {
    display: flex;
    flex-direction: column;
}

.todo-date {
    font-size: 1.5rem;
    color: rgba(0, 0, 0, .5);
    margin-bottom: 1rem;
    font-family: 'Calistoga', cursive;
}

.todo-content {
    font-family: 'Calistoga', cursive;
    margin-right: auto;
    font-size: 2rem;
    color: rgba(73, 0, 92, 0.664);
    word-break: break-all;
    font-weight: 900;
    text-transform: capitalize;
    /* padding: 1.5rem 1rem;        */
    height: 50%;
    border: 2px solid transparent;
    /* border-bottom: 2px solid transparent; */
}

.todo-content-editing-form {
    flex: 1 1;
    display: flex;
    align-items: center;
    margin-right: 2rem;
}

.todo-content-editing-input {
    font-family: 'Calistoga', cursive;
    width: 100%;
    border-radius: 7px;
    height: 50%;
    border: 2px solid transparent;
    -webkit-transition: all .15s linear;
    transition: all .15s linear;
    font-weight: 100;
    padding: 1.5rem 1rem;
    font-size: 2rem;
    background: rgba(117, 2, 184, 0.349);
    outline: none;
}

.todo.done > .todo-content {
    text-decoration: line-through;
    color: rgba(0, 0, 0, 0.253);
    font-weight: 100;
}

.todo-button-group {
    /* padding-top: ; */
    display: flex;
    justify-content: flex-end;
}

.todo-edit-button {
    border: none;
    width: 3rem;
    cursor: pointer;
    background-color: transparent;
    /* margin-right: 1rem; */
    /* box-shadow: 0 0 5px 1px black; */
}

.todo-done-button {
    /* overflow: hidden; */
    border: none;
    font-family: 'Josefin Sans', sans-serif;
    background-color: transparent;
    color: rgba(0, 0, 0, .8);
    cursor: pointer;
    width: 3rem;
    /* margin-right: 1rem; */
    border-radius: 5px 0 0 5px;
    /* box-shadow: 0 0 5px 1px black; */
    background-repeat: no-repeat;
}

.todo-discard-button {
    /* overflow: hidden; */
    border: none;
    background-color: transparent;
    width: 3rem;
    cursor: pointer;
    border-radius: 0 5px 5px 0;
    /* box-shadow: 0 0 5px 1px black; */
}

i {
    color: rgba(117, 22, 50, 0.55);
    -webkit-transition: all .15s linear;
    transition: all .15s linear;
}

button:hover > i {
    color: rgba(167, 0, 182, 0.822);
}
