.list-complex {
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin-top: 3rem;
    grid-column-gap: 5rem;
    grid-row: 3 / auto;
}

.list {
    display: grid;
    align-content: start;
    grid-template-columns: repeat( 10, 1fr);      
    /* grid-auto-flow: row; */
    list-style-type: none;
    grid-row-gap: 2rem;
}  

.list-undone > .todo-item {
    grid-column: 2 /  10;
}

.list-done > .todo-item {
    grid-column: -2 / -10;
}

