.todo-container {
    position: -webkit-sticky;
    position: sticky;
    /* z-index: 1000; */
    top: 0;
    z-index: 1000;
    /* align-self: stretch; */
    grid-row: 1 / span 2;
    grid-column: 1 / span 1;
}

.todo-container.shrink-on-focus-out:not(:focus-within) {
    grid-row: 1 / span 1;
}

.todo-container.shrink-on-focus-out > .todo-input::-webkit-input-placeholder {
    color: transparent;
}

